@import url("https://use.typekit.net/kbi4ctj.css");
@import url('https://fonts.googleapis.com/css?family=Work+Sans:100');



.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.logo-container{
  height: 3.5rem;
  width: 3.5rem;
}
.logo-container svg{
  height: 100%;
  width: 100%;
}

.logo-container path{
  fill: white;
}

.App-header {
  background-color: #222;
  padding: .5rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  font-family: "mr-eaves-xl-sans", arial, sans-serif;
  letter-spacing: .2rem;
}
.brand-wrap{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.App-title {
  font-size: 2.2rem;
  text-transform: uppercase;
  letter-spacing: .5rem;
  line-height: 1rem;
  color: #34e5ea;
  margin-top: 2rem;
  font-weight: 200;
  padding: 1.2rem;
  margin: .8rem;
  font-family: 'Work Sans', sans-serif;
  /* border-left: solid 1px white; */
}

.App-intro {
  font-size: large;
}

.kep-login-facebook.metro{
  background: none;
  border: none;
  padding: .5rem 1rem;
  border-top: solid 2px #4c69ba;
  border-bottom: solid 2px #4c69ba;

}

.kep-login-facebook.metro:hover{
  background: #4c69ba;
  padding: .5rem;
  border-top: solid 2px #4c69ba;
  border-bottom: solid 2px #4c69ba;
}


@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



